export default {
	data() {
		return {
			isfirst:true,
			headerPosition:"fixed",
			headerTop:null,
			statusTop:null,
			showHeader:true,
			
			//用户名 需要去请求的
			username:'',
			//个人信息,
			
			user:{
				face:'/static/img/face.jpg',
				signature:'点击昵称跳转登录/注册页',
				integral:0,
				balance:0,
				envelope:0
			},
			// 订单类型
			orderList:[
				{text:'待付款',icon:"fukuan"},
				{text:'待发货',icon:"fahuo"},
				{text:'待收货',icon:"shouhuo"},
				{text:'待评价',icon:"pingjia"},
				{text:'退换货',icon:"tuihuo"}
			],
			// 工具栏列表
			mytoolbarList:[
				{url:'../../user/keep/keep',text:'我的报名',img:'/static/img/user/point.png'},
				{url:'../../user/coupon/coupon',text:'券',img:'/static/img/user/quan.png'}, 
				{url:'',text:'豪礼',img:'/static/img/user/renw.png'},
				{url:'',text:'领红包',img:'/static/img/user/momey.png'},
				{url:'../../user/address/address',text:'常用地址',img:'/static/img/user/addr.png'},
				{url:'',text:'账户安全',img:'/static/img/user/security.png'},
				{url:'',text:'银行卡',img:'/static/img/user/bank.png'},
				{url:'',text:'抽奖',img:'/static/img/user/choujiang.png'},
				// {text:'客服',img:'/static/img/user/kefu.png'},
				// {text:'签到',img:'/static/img/user/mingxi.png'}
				
			]
		}
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
	    setTimeout(function () {
	        uni.stopPullDownRefresh();
	    }, 1000);
	},
	onPageScroll(e){
		//兼容iOS端下拉时顶部漂移
		this.headerPosition = e.scrollTop>=0?"fixed":"absolute";
		this.headerTop = e.scrollTop>=0?null:0;
		this.statusTop = e.scrollTop>=0?null:-this.statusHeight+'px';
	},
	onLoad() {
		var self=this;
		
		this.statusHeight = 0;




		// 判断是否登录
		if(global.islogin()==false){
			// 未登录就跳转到login页面，在全局去定义islogin函数
			uni.reLaunch({
				url:'../../login/login'
			})
		}else{
			//登录就保存token信息 李杰  
			uni.request({
				url:self.$url+'v1.user/index',
				method:'POST',
				header:{
					token:global.islogin()
				},
				success:function(res){
					if(res.statusCode==200){
						self.username=res.data.username;
					}
				},
				complete:function(res){//判断是否token过期
					if(res.statusCode!=200){
						//跳转到登录页面
						uni.reLaunch({
							url:'../../login/login'
						})
					}
				}
			})
		}
		
		
	},
	onReady(){
		//此处，演示,每次页面初次渲染都把登录状态重置
		uni.setStorage({
			key: 'UserInfo',
			data: false,
			success: function () {
			},
			fail:function(e){
			}
		});
	},
	onShow(){
		uni.getStorage({
			key: 'UserInfo',
			success: (res)=>{
				if(!res.data){
					if(this.isfirst){
						//this.toLogin();
					}
					return ;
				}
				this.user = res.data;
			},
			fail:(e)=>{
				//this.toLogin(); 
			}
		});
	},
	methods: {
		//消息列表
		toMsg(){
			uni.navigateTo({
				url:'../../msg/msg'
			})
		},
		toOrderList(index){
			uni.setStorageSync('tbIndex',index);
			uni.navigateTo({url:'../../user/order_list/order_list?tbIndex='+index}) 
		},
		toSetting(){
			uni.navigateTo({
				url:'../../user/setting/setting'
			})
		},
		toMyQR(){
			uni.navigateTo({
				url:'../../user/myQR/myQR'
			})
		},
		toDeposit(){
			uni.navigateTo({
				url:'../../user/deposit/deposit'
			})
		},
		toPage(url){
			if(!url){
				uni.showToast({title: '正在筹备中',icon:"none"});return;
			}
			uni.navigateTo({
				url:url
			})
		}
	}
} 
